.App {
  text-align: center;

    .App-header {
      background-color: #291225;
      min-height: 100vh;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: calc(10px + 2vmin);
      color: white;

      .App-logo {
        height: 40vmin;
        pointer-events: none;
      }

      .App-link {
        color: #61dafb;
      }
    }
}